<template>
  <div class="login-container">
    <form class="form" @submit.prevent="onClickSubmit">
      <h3>로그인 후 내용을 확인해주세요.</h3>

      <label class="label">아이디</label>
      <input type="text" size="8" v-model="id" class="input" />
      <label class="label">비밀번호</label>
      <input type="password" size="8" v-model="pwd" class="input" />
      <button type="submit" class="button"> 로그인 </button>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: "",
      pwd: "",
    };
  },
  methods: {
    onClickSubmit() {
      if (this.id === "id" && this.pwd === "0000") {
        // 로그인 성공
        this.$emit("login-success");
      } else {
        // 로그인 실패
        alert("아이디 또는 비밀번호가 올바르지 않습니다.");
      }
    },
  },
};
</script>

<style>
.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
  }
  
  .form {
    border: 1px solid #ccc;
    padding: 100px;
    border-radius: 5px;
    background-color: #f0f0f0;
    width: 300px;
  }
  
  .label {
    margin-bottom: 10px;
    font-weight: bold;
  }
  
  .input {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 3px;
  }
  
  .button {
    background-color: #007BFF;
    color: #fff;
    padding: 10px 20px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
  }
  
  .button:hover {
    background-color: #0056b3;
  }</style>